import React from "react";
import { Link, BrowserRouter } from "react-router-dom";
import "./App.css";

function App() {
  return (
    <div className="App">
      {/* //  */}
      <BrowserRouter>
        <h3>
          <Link to="/qk-micro-react">React</Link>
        </h3>
        <h3>
          <Link to="/qk-micro-vue">Vue</Link>
        </h3>
      </BrowserRouter>
      {/* id  registerMicroApps  container  */}
      {/* react id=qk-micro-react */}
      <div id="qk-micro-react" />
      {/* vue id=qk-micro-vue  */}
      <div id="qk-micro-vue" />
    </div>
  );
}

export default App;
